.pokedex-container {
  height: 595px;
  width: 535px;
  background-color: whitesmoke;
  border: 0;
  border-radius: 10px;
  margin: 40px auto;
  text-align: center;
  box-shadow: 8px 8px 0 skyblue;
}

.title {
  margin: 40px 0 10px;
  font-size: 30px;
  font-weight: 200; 
}

.pokedex-list {
  display: flex;
  flex-flow: column nowrap;
}