body {
  font-family: 'Poppins', sans-serif;
}

p {
  line-height: 6px;
  font-size: 16px;
  font-weight: 300;
}

.pokemon-container {
  height: 200px;
  width: 400px;
  border: 1px solid lightgray;
  border-radius: 10px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  margin: 20px auto;
}

.pokemon-container:hover {
  box-shadow: 2px 2px 6px gainsboro, -2px -2px 6px gainsboro;
}

.pokemon-container:last-child {
  margin-bottom: 40px;
}

.pokemon-list {
  display: flex;
  flex-flow: column nowrap;
}

.pokemon-list a, .pokemon-list p {
  margin: 15px 0;
}

.avi {
  width: 100px;
  height: 100px;
}

.more {
  background-color: #dfdfdf;
  border: 1px solid #bbb;
  border-radius: 6px;
  padding: 5px;
  width: 90px;
  font-size: 13px;
  font-weight: 300;
  margin-top: 3px;
  cursor: pointer;
}